<template>
  <div class="update-name">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <!-- / -->

    <div class="name-field-wrap">
      <van-field
        v-model="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      />
    </div>

    <!-- <button @click="$emit('update:abc', 0)"> gender</button> -->
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'

export default {
  name: 'UpdateName',
  components: {},
  props: {
    //  v-model  value 
    value: {
      type: String,
      required: true
    }
    // abc: {
    //   type: Number
    // }
  },
  data () {
    return {
      // var a = 1
      // var b = a
      // b = 123
      // a? 1
      localName: this.value
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '',
        forbidclick: true // 
      })
      try {
        // 
        await updateUserProfile({
          name: this.localName
        })

        //  ->  name -> 
        //  input  v-model 
        this.$emit('input', this.localName)
        // this.$emit('update-name', this.localName)
        this.$emit('close')

        this.$toast.success('')
      } catch (err) {
        if (err && err.response && err.response.status === 409) {
          this.$toast.fail('')
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
.name-field-wrap {
  padding: 10px;
}
</style>
